.task-list {
    flex: 1 1 auto;
    flex-direction: column;
    position: relative;
    gap: 8px;
    margin: 0 rem(4px);
    overflow-x: hidden;
    overflow-y: auto;
    padding: 0 rem(4px);

    &::-webkit-scrollbar {
        display: none;
    }

    @include for-narrow-layout {
        @include scrollbar(8px, 2rem, rgba(9, 30, 66, .13), rgba(9, 30, 66, .08));
    }
}