.window-sidebar-box {
    width: 192px;
    float: right;
    overflow-y: visible;
    padding: 0 16px 8px 8px;

    @media (max-width: 750px) {
        padding: 8px;
        width: 100%;

    }

    .sidebar-title {
        @include details-label;
    }

    @include for-native-layout-details {
        order: 1;
        margin-bottom: 2px;
        border-block-end: 1px solid rgb(219, 218, 218);

    }
}

.window-sidebar {
    position: relative;
    overflow-y: visible;
    margin-block-end: 24px;

    @media (max-width: 750px) {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 4px;
        max-width: 100%;
        margin: 0;
    }


    @include for-native-layout-details {
        .window-sidebar.flex {
            display: flex;
            flex-direction: unset;
            flex-wrap: wrap;
            width: 100%
        }
    }
}


.button-link {
    @include task-nav-button;
    position: relative;
    text-align: left;

    &-header {
        @include task-nav-button;
        margin: 0 0 0 8px;
        padding-inline: 12px;

        &:hover {
            background-color: #e2e5e9;
            text-decoration: none;
        }
    }

    &:hover {
        background-color: #e2e5e9;
        text-decoration: none;
    }

    @include for-native-layout-details {
        @include button-filled;
        margin: 0;
        width: 100%;
    }

    .nav-btn-txt {
        line-height: 20px;
        vertical-align: text-bottom;
    }


}

.sidebar-icon {
    @include for-native-layout-details {
        color: #fff;
        background-color: $button-filled;
        border-radius: 50%;
        padding-block: 16px;

    }
    margin: 0 6px 0 -6px;
}